.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-family: "MuseoSansCyrl", sans-serif;
  text-shadow: 0.5px 0.9px 1px rgba(26, 30, 29, 0.32);
  font-size: 18px;
  font-weight: 900;
  line-height: 1;
  text-decoration: none;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  backface-visibility: hidden;
}

@media only screen and (max-width: 1023px) {
  .btn {
    font-size: 15px;
  }
}

@media only screen and (max-width: 767px) {
  .btn {
    font-size: 12px;
  }
}

.btn:hover .btn__inner {
  transform: translateY(4px);
}

.btn:hover:after {
  transform: translateY(0) scaleY(0.8);
}

.btn__inner {
  position: relative;
  z-index: 2;
  width: 100%;
  padding: 25px 32px;
  background-color: #34c399;
  border-radius: 4px;
  transition: transform 0.3s;
}

@media only screen and (max-width: 1023px) {
  .btn__inner {
    padding: 20px 24px;
  }
}

@media only screen and (max-width: 767px) {
  .btn__inner {
    padding: 14px 16px;
  }
}

.btn:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateY(4px);
  z-index: -1;
  content: '';
  background-color: #216d57;
  border-radius: 4px;
  transition: transform 0.3s;
}

