.hero {
  background-image: url(./../img/hero.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 0;
  color: $titleWhite;
  height: 60.57vw;
  min-height: 900px;
  @include lg {
    background-position: 50% 0; }
  @include sm {
    background-position: 60% 0; }
  &__header {
    padding-top: 10vw;
    margin-bottom: 70px;
    width: 58.333%; }
  &__title {
    margin-bottom: 30px;
    span {
      display: inline-block; } }
  &__descr {
    padding-right: 100px;
    @include lg {
      padding-right: 0; }
    p:not(:last-child) {
      margin-bottom: 26px; } }
  &__features {
    margin-bottom: 45px; }
  &__cta {
    text-align: center; } }

.feature {
  text-align: center;
  @include sm {
    display: flex; }
  @include hover {
    .feature__icon:before {
      transform: scaleX(2); }
    .feature__icon:after {
      transform: scaleY(2); } }
  &__icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 50px;
    width: 50px;
    margin-bottom: 32px;
    transform: rotate(45deg);
    overflow: hidden;
    @include md {
      width: 40px;
      height: 40px; }
    @include sm {
      margin-bottom: 10px; }
    @include sm {
      width: auto;
      height: auto; }
    &:before,&:after {
      content: ' ';
      position: absolute;
      border-color: $white;
      border-width: 1px;
      transition: transform $tr;
      @include sm {
        display: none; }
      .feature_restriction & {
        border-color: #b2c1cb; } }
    &:before {
      width: 60%;
      height: 100%;
      border-top-style: solid;
      border-bottom-style: solid; }
    &:after {
      height: 60%;
      width: 100%;
      border-left-style: solid;
      border-right-style: solid; }
    [class*='icon-'] {
      transform: rotate(-45deg); }
    .icon-check {
      color: $mainGreen;
      font-size: 1.125em; }
    .icon-times {
      color: $coral;
      font-size: 1em; } }
  &__descr {
    font-family: Montserrat;
    font-size: 1.125em;
    font-weight: 300;
    line-height: 1.33;
    @include sm {
      padding-left: 10px;
      br {
        display: none; } } } }
